import { Component, OnInit, Output, EventEmitter, Input, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { AuthData } from 'src/app/api/models';
import { nifValidator } from '@shared/validators';
import { ExternalLinks, EXTERNAL_LINKS } from '@core/constants/external-links.constants';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styles: [],
})
export class LoginFormComponent implements OnInit {
  @Input() invalidCredentials = false;
  @Output() login = new EventEmitter<AuthData>();
  @Output() recover = new EventEmitter<boolean>();

  form: FormGroup;
  submitted = false;

  constructor(
    private formBuilder: FormBuilder,
    @Inject(EXTERNAL_LINKS) public externalLinks: ExternalLinks
  ) {}

  ngOnInit(): void {
    this.createForm();
  }

  onSubmitForm() {
    this.submitted = true;

    if (this.form.valid) {
      const { username, password } = this.form.value;
      this.login.emit({ username, password });
    }
  }

  hasError(control: AbstractControl) {
    return control.touched && control.invalid;
  }

  private createForm() {
    this.form = this.formBuilder.group({
      username: ['', [Validators.required, nifValidator]],
      password: ['', Validators.required],
    });
  }

  recoverPasswInfo() {
    this.recover.emit(true);
  }
}
