import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder, FormGroup, AbstractControl, Validators } from '@angular/forms';
import { passwordFormat, matchValidator } from '@core/utils';

@Component({
  selector: 'app-activate-user-form',
  templateUrl: './activate-user-form.component.html',
  styles: [],
})
export class ActivateUserFormComponent implements OnInit {
  @Output() changePassword = new EventEmitter<string>();
  form: FormGroup;
  submitted = false;

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.createForm();
  }

  onSubmit() {
    this.submitted = true;
    this.form.markAllAsTouched();

    if (this.form.valid) {
      this.changePassword.emit(this.form.value.password);
    }
  }

  hasError(control: AbstractControl) {
    return control.touched && control.invalid;
  }

  private createForm() {
    this.form = this.formBuilder.group(
      {
        password: ['', [Validators.required, Validators.pattern(passwordFormat)]],
        passwordConfirm: ['', [Validators.required, Validators.pattern(passwordFormat)]],
      },
      { validators: matchValidator('password', 'passwordConfirm') }
    );
  }
}
