import { Component, OnInit, OnDestroy, ViewChild, TemplateRef, Inject } from '@angular/core';
import { AuthService } from '@core/services/authentication/auth.service';
import { Subscription } from 'rxjs';
import { AuthData, ApiError } from '../api/models';
import { Router } from '@angular/router';
import { ApiErrors } from '@api/constants';
import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { StorageService, STORAGE_KEY } from '@core/services/storage/storage.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { encrypt, PUBLIC_KEY, ALGORITHM } from '@core/utils';
import { CUSTOMER_SUPPORT, CustomerSupport } from '@core/constants/info.constants';
import { environment } from '@environment/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styles: [],
})
export class LoginComponent implements OnInit, OnDestroy {
  @ViewChild('otpModal') otpModal;
  @ViewChild('recoverModal') recoverModal;
  @ViewChild('activateUserModal') activateUserModal;

  token: string;
  userBe: string;
  invalidCredentials = false;

  private modalRef: BsModalRef;
  private credentials: AuthData;
  private subs = new Subscription();
  homeLink = environment.applications.home;

  constructor(
    private authService: AuthService,
    private router: Router,
    private errorHandler: ErrorHandlerService,
    private modalService: BsModalService,
    private storageService: StorageService,
    @Inject(CUSTOMER_SUPPORT) public customerSupport: CustomerSupport
  ) {}

  ngOnInit() {}

  ngOnDestroy() {
    this.closeModal();
    this.subs.unsubscribe();
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  closeModal() {
    if (this.modalRef) {
      this.modalRef.hide();
    }
  }

  async login(credentials: AuthData, otpLogin?: string) {
    this.credentials = credentials;

    const password = await encrypt(credentials?.password, PUBLIC_KEY, ALGORITHM);

    this.subs.add(
      this.authService
        .authenticate(
          {
            username: credentials?.username?.toUpperCase(),
            password,
          },
          otpLogin
        )
        .subscribe(
          (result) => {
            this.token = result.access_token;
            this.userBe = result.userBe;

            if (this.userBe) {
              this.openModal(this.activateUserModal);
            } else {
              this.saveTokenAndNavigate();
            }
          },
          (error: ApiError) => {
            this.handleLoginError(error);
          }
        )
    );
  }

  async onChangePassword(newPassword: string) {
    const encryptedPassword = await encrypt(newPassword, PUBLIC_KEY, ALGORITHM);

    this.closeModal();
    this.subs.add(
      this.authService.activate(this.userBe, encryptedPassword).subscribe(
        () => {
          this.saveTokenAndNavigate();
        },
        (error) => {
          this.errorHandler.showError(error);
        }
      )
    );
  }

  onSendOtp(secretKey: string) {
    this.closeModal();
    this.login(this.credentials, secretKey);
  }

  onRepeatOtp() {
    this.login(this.credentials);
  }

  private saveTokenAndNavigate() {
    this.storageService.setItem(STORAGE_KEY.TOKEN, this.token);
    this.router.navigate(['']);
  }

  private handleLoginError(error: ApiError) {
    switch (error.errorCode) {
      case ApiErrors.AUTH_ERROR_OTP_REQUIRED:
        this.openModal(this.otpModal);
        break;

      case ApiErrors.AUTH_ERROR_BAD_REQUEST:
        this.invalidCredentials = true;
        break;

      default:
        this.errorHandler.showError(error);
        break;
    }
  }

  showRecoverModal() {
    this.modalRef = this.modalService.show(this.recoverModal);
  }

  hideRecoverModal() {
    this.modalRef.hide();
  }
}
